body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.allBtns {
  cursor: pointer;
  background-color: white;
  color: #444;
}
.allBtns:hover {
  background-color: #444;
  color: white;
  -webkit-transition: background-color 200ms linear, color 200ms linear;
  -o-transition: background-color 200ms linear, color 200ms linear;
  transition: background-color 200ms linear, color 200ms linear;
}
.mainBtn {
  cursor: pointer;
  background-color: white;
  color: #444;
  height: 40px;
  width: 100px;
}
.mainBtn:hover {
  background-color: #444;
  color: white;
  -webkit-transition: background-color 200ms linear, color 200ms linear;
  -o-transition: background-color 200ms linear, color 200ms linear;
  transition: background-color 200ms linear, color 200ms linear;
}
.mdBtn {
  cursor: pointer;
  background-color: white;
  color: #444;
  border: #444 solid 1px;
  height: 33px;
  padding-top: 3px;
  width: 70px;
}
.mdBtn:hover {
  background-color: #444;
  color: white;
  -webkit-transition: background-color 200ms linear, color 200ms linear;
  -o-transition: background-color 200ms linear, color 200ms linear;
  transition: background-color 200ms linear, color 200ms linear;
}
.miniBtn {
  cursor: pointer;
  background-color: white;
  color: #444;
  height: 20px;
  width: 50px;
  font-size: 12px;
}
.miniBtn:hover {
  background-color: #444;
  color: white;
  -webkit-transition: background-color 200ms linear, color 200ms linear;
  -o-transition: background-color 200ms linear, color 200ms linear;
  transition: background-color 200ms linear, color 200ms linear;
}
.textBar {
  border: #444 solid 1px;
  height: 33px;
}



