div,
button {
  margin: 10px;
}
#select {
  font-size: 14px;
  height: 25px;
  width: 40px;
  margin-left: 35px;
}
.cover {
  max-height: 100px;
  max-width: 100px;
}
.words {
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 24px;
  text-align: center;
}
.buttons {
  margin-top: 50px;
}
.maxRecSection {
  padding-bottom: 15px;
  padding-top: 5px;
}
#playlistsSection {
  margin-left: 50px;
  margin-top: -5px;
}
.settingsWords {
  font-size: 20px;
}
.playlist {
  display: -ms-flexbox;
  display: flex;
  width: 267px;
  border: solid 2px white;
  background-color: #444;
  cursor: pointer;
  -webkit-transition: border-color 200ms linear, color 200ms linear;
  -o-transition: border-color 200ms linear, color 200ms linear;
  transition: border-color 200ms linear, color 200ms linear;
}
.playlistActive {
  color: black;
  border: solid 2px black;
}
.playlistRight {
  position: relative;
  padding-top: 40px;
}
.playlistRightInners {
  width: 140px;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  margin-top: -10px;
}
.Bottom {
  padding-top: 10px;
  text-align: center;
}
.customBtn {
  font-size: 14px;
  background-color: white;
  color: #555;
  border: 1px solid white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin: 4px 2px;
}
.customBtn:hover {
  background-color: #555;
  color: white;
  cursor: pointer;
}
.customBtn:focus {
  outline: none;
}
.topPart {
  text-align: center;
  font-size: 24px;
  padding-top: 6px;
  padding-bottom: 10px;
}
.code {
  text-align: center;
  font-size: 16px;
  padding: 8px;
}
.code2 {
  text-align: center;
  font-family: "Do Hyeon";
  font-weight: bold;
  font-size: 24px;
}
.divider {
  border: white 1px solid;
  max-width: 500px;
  min-width: 200px;
}
.divider4 {
  border: white 1px solid;
}
.recommendedSongs {
  text-align: center;
}
@media (max-width: 650px) {
  .recommendedSongs {
    text-align: left;
  }
}
.selections {
  margin-top: 5px;
  margin-bottom: 5px;
  -ms-flex-align: flex;
      align-items: flex;
  white-space: nowrap;
}
.recommendMe {
  margin-left: 5px;
}
.customBtn2 {
  font-size: 11px;
  background-color: white;
  color: #555;
  border: 1px solid white;
  padding: 2px 4px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin: 4px 2px;
}
.customBtn2:hover {
  background-color: #555;
  color: white;
  cursor: pointer;
}
.customBtn2:focus {
  outline: none;
}
.deleteMe {
  margin-left: -1px;
}
#maxSpan {
  font-size: 14px;
  margin-left: 5px;
  text-align: center;
  margin-top: 15px;
}
.logout {
  margin-left: 7px;
}
.italicize {
  font-style: italic;
}



